import "./index.scss";
import { connect } from "react-redux";
import {
  getDetail,
  getConllect,
  changeConllect,
  getWordLike,
  changeWordLike
} from "../../actions";
import { history } from "&/history";
import { List } from "antd-mobile";
const Item = List.Item;
@connect(state => {
  return {
    word: state.getIn(["data", "word"]),
    isShuqian: state.getIn(["data", "isShuqian"]),
    sNum: state.getIn(["data", "sNum"]),
    isXihuan: state.getIn(["data", "isXihuan"]),
    xNum: state.getIn(["data", "xNum"])
  };
})
export default class Detail extends Component {
  constructor() {
    super();
    this.state = {
      tNum: 0,
      pic: require("@/assets/images/touxiang.jpg")
    };
  }
  changeShuqian = () => {
    let wordId = this.props.match.params.wordId;
    let word = this.props.word;
    this.props.dispatch(
      changeConllect("/react/changeConllect", {
        wordId,
        word
      })
    );
  };
  changeTaolun = wordId => {
    history.push(`/talkabout/${wordId}`);
  };
  changeXihuan = () => {
    let wordId = this.props.match.params.wordId;
    let word = this.props.word;
    this.props.dispatch(
      changeWordLike("/react/changeWordLike", {
        wordId,
        word
      })
    );
  };
  goBack = () => {
    history.go(-1);
  };
  componentDidMount() {
    let wordId = this.props.match.params.wordId;
    this.props.dispatch(
      getDetail({
        url: "/react/getDetail",
        params: {
          wordId
        }
      })
    );
    this.props.dispatch(getConllect("/react/getConllect", { wordId }));
    this.props.dispatch(getWordLike("/react/getWordLike", { wordId }));
  }

  render() {
    const { word, isShuqian, sNum, isXihuan, xNum } = this.props;
    const { tNum, pic } = this.state;
    console.log(isXihuan);
    return (
      <div>
        <List>
          <Item thumb={pic}>{word.creator}</Item>
        </List>
        <div style={{ height: "0.9rem", padding: "0.1rem" }}>
          <img
            src={word.img}
            alt=""
            style={{ width: "100%", height: "4rem", marginBottom: "1.6rem" }}
          />
          <div
            className="content"
            style={{ position: "relative", padding: "0.4rem" }}
          >
            <p
              className="hitokoto"
              style={{
                height: "2.5rem",
                marginBottom: "0.2rem"
              }}
            >
              {word.hitokoto}
            </p>
            <p
              className="fromTitle"
              style={{
                textAlign: "center",
                overflow: "hidden",
                width: "100%",
                whiteSpace: "nowrap",
                textOverflow: "ellipsis"
              }}
            >
              -《{word.from}》-
            </p>
          </div>
        </div>
        <div
          style={{
            display: "flex",
            borderTop: "1px solid #eee",
            paddingTop: "0.1rem",
            position: "fixed",
            bottom: 0,
            width: "100%",
            left: 0,
            height: "0.9rem"
          }}
        >
          <p className="iconDom">
            <i className="iconfont icon-fanhui" onClick={this.goBack}></i>
          </p>
          <p className="iconDom">
            <i
              className={
                isShuqian
                  ? "iconfont icon-shuqian_B"
                  : "iconfont icon-shuqian_A"
              }
              style={{ marginRight: "0.1rem" }}
              onClick={this.changeShuqian}
            ></i>
            <span>{sNum}</span>
          </p>
          <p className="iconDom">
            <i
              className="iconfont icon-msg"
              style={{ marginRight: "0.1rem" }}
              onClick={() => this.changeTaolun()}
            ></i>
            <span>{tNum}</span>
          </p>
          <p className="iconDom">
            <i
              className={
                isXihuan ? "iconfont icon-xihuan" : "iconfont icon-like"
              }
              style={{ marginRight: "0.1rem" }}
              onClick={this.changeXihuan}
            ></i>
            <span>{xNum}</span>
          </p>
        </div>
      </div>
    );
  }
}
